.divider{
    text-align: center;
    display: flex;
    justify-content: center;
    align-items: center;
    /* padding: 20px 10px; */
}
.divider p{
    display: block;
    flex: 0.7 1;
    padding: 0;
    margin-top: 25px;
    font-size: 26px;
    font-weight: 600;
}
.left-line{
    border:0.1px solid rgba(18, 18, 18, 0.35);
    flex: 1 1;
}
.right-line{
    border:0.1px solid rgba(18, 18, 18, 0.35);
    flex: 1 1;
}


.productContainer{
    width: 80%;
    /* padding: 20px; */
    /* height: auto; */
    margin: 40px auto 10px auto; 
    /* display: grid;
    grid-template-columns: repeat(auto-fit, minmax(300px, 1fr)
    );  */
     /* grid-template-columns: minmax(200px, max-content)
                       repeat(auto-fill, 300px) 25%;  */
    /* grid-template-columns: 300px repeat(auto-fill, 300px) 300px; */
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 2rem;
    padding:20px;
    height:auto;
    /* grid-gap: 1rem; */
}
.popularProduct-container{
  /* width: 250px; */
  height: auto;
  
}
/* product wrapper styling */
.loader-box{
  display: flex;
  justify-content: center;
  align-items: center;
  width: 90vw;
  margin: 0 auto;
}
.productWrapper{
  margin: 20px 0;
  height: auto;
  /* width: 247px; */
  
}
.prod-img-wrapper{
  /* width: 245px; */
  height: 450px;
  
}
.prod-img-wrapper img{
    display: block;
    width: 100%;
    height: 100%;
    transition: -webkit-transform 4s;
    transition: transform 4s;
    transition: transform 4s, -webkit-transform 4s;
    cursor: pointer;
    object-fit: cover;
}

.prod-img-wrapper:hover img{
    -webkit-transform: scale(1.01);
            transform: scale(1.01);
    transition: all 0.5s ease-in;
}
/* product wrapper footer */
.productWrapper-footer{
  margin: 10px 0;
  padding: 10px;
  display: flex;
  flex-direction: column;
}
.inner{
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}
.product-details-foot{
  width: 100%;
  /* text-align: left; */
  display: flex;
  justify-content: space-between;
  align-items: center;

}
.product-details-foot .foot-prod-name{
  width: 60%;
  text-align: left;
}
.disc-price h4:nth-child(1){
  text-decoration: line-through;
}
.disc-price h4:nth-child(2){
  color: darkred;
  font-size: 15px;
}
/* product.discoutPrice */
.product-details-foot .foot-prod-price{
  display: flex;
  justify-content: space-around;
  align-items: center;
  width: 40%;
  /* text-align: right; */
}
.footer-icons{
  width: 30%;
}
.ratings{
  width: 70%;
  display: flex;
  /* justify-content: center;
  align-items: center; */
}
.star-group{
  flex: 1 1;
}
.reviews{
  flex: 1 1;
  padding: 0;
  margin-top: -5px;
}
.ratings .MuiSvgIcon-root{
  font-size: 20px !important;
}
.footer-icons{
  display: flex;
  justify-content: space-around;
}
.footer-icons .MuiSvgIcon-root{
  font-size: 20px !important;
}
.product-details-foot h4{
  font-size: 18px;
}
span{
  font-size: 14px;
  color: rgba(18, 18, 18, 0.75)
}

/* media query */
@media screen and (max-width:576px) {
    .productContainer {
        height: auto;
        width: 100%;
        display: grid;
        grid-template-columns: repeat(auto-fit, minmax(100%, 1fr)
        );
        grid-gap: 1rem;
    }

    .popularProduct-container{
      /* width: 250px; */
      height: auto;
    }
    /* product wrapper styling */
  
    .productWrapper{
      height: auto;
    }
    
    .prod-img-wrapper{
      width: 100%;
      height: 65vh;      
    }
    .prod-img-wrapper img{
        display: block;
        width: 100%;
        height: 100%;
        transition: -webkit-transform 4s;
        transition: transform 4s;
        transition: transform 4s, -webkit-transform 4s;
        cursor: pointer;
        object-fit: cover;
    }

   
  
    .inner{
      width: 100%;
     flex-direction: column;
    }
    .ratings{
      width: 100%;
    }
    .footer-icons{
      width: 100%;
    }
    .star-group{
      width: 100%;
    }
    .product-details-foot{
      width: 100%;
      text-align: center;
    }
  }
  
  /* @media screen and (min-width:992px) {
    .productContainer {
      display: grid;
      grid-template-columns: repeat(1, 1fr);
      grid-column-gap: 1em;
    }
  } */
  

.single-product-inner-container{
    display: flex;
    flex-direction: row;
    margin: 50px auto;
    padding: 50px;
    height: auto;
}
.image-cont{
    display: flex;
    flex-direction: column;
    width: 50%;
    padding: 5px 20px;
    height: 550px;
}
.image-cont img{
    /* width: 100%;
    max-width: 100%;
    height: 70%; */
    width: 100%;
    height: 100%;
    margin: auto;
    display: block;
    object-fit: contain;
    margin: 10px 0;
}
.image-cont video{
    width: 100%;
    max-width: 100%;
    height: 70%; 
    margin: 10px 0;
}
.product-desc{
    width: 50%;
    padding: 0 50px;
}

.product-desc h2{
    color: hsl(0, 0%, 0%);
    font-size: 2.13rem;
    font-weight: 600;
    text-align: left;
    text-transform: uppercase;
    letter-spacing: 1.5px;
}
.product-desc p{
    /* font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif; */
    font-size: 14px;
    font-weight: 400;
    color: #000;
    opacity: 75%;
    font-weight: 500;
    letter-spacing: 1.5px;
}
.product-desc h3{
    color: hsl(0, 0%, 0%);
    font-size: 2.13rem;
    font-weight: 600;
    text-align: left;
    text-transform: uppercase;
    letter-spacing: 1.1px;
}

.filter-cotainer{
    width: 100%;
    display: flex;
    /* justify-content: center; */
    align-items: center;
    height: 10vh;
    margin: 20px auto;
}
.filter-box{
    margin: 0 20px;
    width: 50%;
}
.filter-cotainer .filter-box select{
    width: 100%;
    padding: 5px 10px;
    border: 0.4px solid #C09621;
    border-radius: 5px;
}

.addContainer{
    width: 100%;
    display: flex;
    flex-direction: column;
    height: 30vh;
    justify-content: space-between;
    align-items: center;
    padding: 0 10px;
}
.add-to-cart{
    border: none;
    background: rgba(191, 149, 33, 0.95);
    color: #fff;
    padding:10px;
    border-radius: 10px;
    font-weight: 600;
    cursor: pointer;
    height: 30%;
    width: 50%;
    margin: 0 auto;
}
.add-to-cart:hover{
    background-color: #fff;
    color:#C09621;
    border: 1px solid #C09621;
}
.amount-container{
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 50%;
}
.amount-container .MuiSvgIcon-root{
    margin: 0 20px;
    cursor: pointer;
}
.amount-container span{
    /* margin: 0 20px; */
    color: black;
    font-weight: 100;
    border: 1px solid #C09621;
    padding: 5px 10px;
    font-size: 19px;
    border-radius: 5px;
}

@media screen and (max-width: 560px) {
    .single-product-inner-container{
        display: flex;
        flex-direction: column;
        width: 100%;
        padding: 10px 5px;
        margin:10px auto;
        height: auto;
        /* text-align: center; */
    }
    .image-cont{
        width: 100%;
        padding: 5px;
        margin: 10px auto;
        height: 70vh;
    }
    .image-cont img{
        width: 100%;
        max-width: 100%;
        height: 100%;
    }


    .product-desc{
        width: 100%;
        padding: 5px 10px;
        margin: 65px auto 20px auto;
        height: auto;
    }
    .product-desc h2{
        color: hsl(0, 0%, 0%);
        font-size: 2.23rem;
        font-weight: 700;
        text-align: left;
        text-transform: uppercase;
        letter-spacing: 1.5px;
    }
    .product-desc p{
        font-family: sans-serif;
        font-size: 1.29rem;
        font-weight: bold;
        color: rgba(15, 15, 15,0.85);
        letter-spacing: 1.5px;
    }
    .product-desc h3{
        color: hsl(0, 0%, 0%);
        font-size: 2.23rem;
        font-weight: 700;
        text-align: left;
        text-transform: uppercase;
        letter-spacing: 1.1px;
    }
    .filter-cotainer{
        width: 100%;
        display: flex;
        flex-direction: row;
        margin: 0 auto;
        height: auto;
    }
    .filter-box{
        width: 50%;
        margin: 20px 5px;
    }
    .filter-cotainer .filter-box select{
       font-weight: bold;
    }

    .addContainer{
        width: 100%;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        align-items: center;
        padding: 5px 10px;
        height: 30vh;
    }
    .add-to-cart{
        padding: 10px;
        font-weight: bold;
        cursor: pointer;
        width: 100%;
        margin: 10px auto;
        text-align: center;
        align-items: center;
        height: 70px;
    }
    .amount-container{
        width: 100%;
        display: flex;
        flex-direction: row;
        justify-content: center;
        align-items: center;
        margin: 10px auto;
        height: 50%;
    }
    .amount-container .MuiSvgIcon-root{
        margin: 0 10px;
    }
    .amount-container span{
        margin: 0 10px;
        font-weight: 300;
    }
}


/* 375px */

@media screen and (max-width: 375px) {

    .single-product-inner-container{
        width: 100%;
        margin: 10px auto;
    }
    .image-cont{
        width: 100%;
        padding: 5px;
        margin: 10px auto;
        height: 70vh;
    }

    .product-desc{
        width: 100%;
        padding: 5px 10px;
        margin: 95px auto 20px auto;
        height: auto;
    }
    .product-desc h2{
        color: hsl(0, 0%, 0%);
        font-size: 2.1rem;
        font-weight: 600;
        text-align: left;
        text-transform: uppercase;
        letter-spacing: 1.2px;
    }
    .product-desc p{
        font-family: sans-serif;
        font-size: 1.39rem;
        font-weight: bold;
        color: rgba(15, 15, 15,0.85);
        letter-spacing: 1.5px;
    }
}
